@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-400.woff2") format("woff2"), url("../fonts/montserrat-400.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-500.woff2") format("woff2"), url("../fonts/montserrat-500.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-600.woff2") format("woff2"), url("../fonts/montserrat-600.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-700.woff2") format("woff2"), url("../fonts/montserrat-700.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Montserrat", "Arial", "Helvetica CY", "Nimbus Sans L", sans-serif;
  color: #011c40;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #f9fbfd;
}

main {
  flex: auto;
}

a {
  color: #011c40;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 32px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.btn {
  display: inline-flex;
  padding: 10px 20px;
  color: #ffffff;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  user-select: none;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.catalog__list {
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 30px;
}

.catalog__item {
  padding: 30px;
  display: flex;
  align-items: flex-end;
  min-height: 386px;
  background-color: #78bcfc;
}
.catalog__item--astoria, .catalog__item--eline {
  background-image: image-set("../img/cruise-1.webp" 1x, "../img/cruise-1.jpg" 1x);
  background-position: top -10px left -20px;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .catalog__item--astoria, .catalog__item--eline {
    background-image: image-set("../img/cruise-1@2x.webp" 2x, "../img/cruise-1@2x.jpg" 2x);
  }
}
.catalog__item--atlantis {
  background-image: image-set("../img/cruise-3.webp" 1x, "../img/cruise-3.jpg" 1x);
  background-position: top -10px left -20px;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .catalog__item--atlantis {
    background-image: image-set("../img/cruise-3@2x.webp" 2x, "../img/cruise-3@2x.jpg" 2x);
  }
}
.catalog__item--odissey {
  background-image: image-set("../img/cruise-4.webp" 1x, "../img/cruise-4.jpg" 1x);
  background-position: top -10px left -20px;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .catalog__item--odissey {
    background-image: image-set("../img/cruise-4@2x.webp" 2x, "../img/cruise-4@2x.jpg" 2x);
  }
}
.catalog__item--olympia {
  background-image: image-set("../img/cruise-5.webp" 1x, "../img/cruise-5.jpg" 1x);
  background-position: top -10px left -20px;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .catalog__item--olympia {
    background-image: image-set("../img/cruise-5@2x.webp" 2x, "../img/cruise-5@2x.jpg" 2x);
  }
}
.catalog__item--aurora {
  background-image: image-set("../img/cruise-6.webp" 1x, "../img/cruise-6.jpg" 1x);
  background-position: top -10px left -20px;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .catalog__item--aurora {
    background-image: image-set("../img/cruise-6@2x.webp" 2x, "../img/cruise-6@2x.jpg" 2x);
  }
}
.catalog__item--leonor {
  background-image: image-set("../img/cruise-7.webp" 1x, "../img/cruise-7.jpg" 1x);
  background-position: top -10px left -20px;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .catalog__item--leonor {
    background-image: image-set("../img/cruise-7@2x.webp" 2x, "../img/cruise-7@2x.jpg" 2x);
  }
}
.catalog__item--minion-1 {
  background-image: image-set("../img/cruise-8.webp" 1x, "../img/cruise-8.jpg" 1x);
  background-position: top -10px left -20px;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .catalog__item--minion-1 {
    background-image: image-set("../img/cruise-8@2x.webp" 2x, "../img/cruise-8@2x.jpg" 2x);
  }
}
.catalog__item h3 {
  padding: 0;
  margin: 0;
  font-weight: 500;
  font-size: 18px;
  line-height: 150%;
  color: #f9fbfd;
}
@media (hover: hover) {
  .catalog__item:hover:not(.focus-visible) {
    padding: 35px 25px;
    background: #011c40;
    box-shadow: 0 4px 35px rgba(1, 28, 64, 0.15);
    align-items: flex-start;
  }
  .catalog__item:hover:not(.focus-visible) h3 {
    display: none;
  }
  .catalog__item:hover:not(.focus-visible) .catalog__item-info {
    display: flex;
    flex-direction: column;
  }
}

.catalog__item-info {
  display: none;
  color: #f9fbfd;
}
.catalog__item-info h4 {
  padding: 0;
  margin: 0 0 14px 0;
  font-weight: 500;
  font-size: 18px;
  line-height: 150%;
}
.catalog__item-info a {
  font-weight: 600;
  font-size: 14px;
  line-height: 150%;
  padding: 9px 46px 10px 47px;
  margin: 0;
  background-color: #f9fbfd;
  color: #011c40;
  justify-content: center;
}

.catalog__item-list {
  list-style-type: none;
  padding: 0;
  margin: 0;
}
.catalog__item-list h5 {
  padding: 0;
  margin: 0 0 2px 0;
  font-weight: 500;
  font-size: 14px;
  line-height: 140%;
}
.catalog__item-list p {
  padding: 0;
  margin: 0 0 11px 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 140%;
}

